// Checkout
.checkout-progress-bar {
	margin: 4.7rem 0 2.1rem;
	font-size: 0;
	line-height: 1.4;

	li {
		display: inline-block;
		position: relative;
		margin-bottom: 3rem;
		color: #999;
		font-family: $second-font-family;
		font-size: 2rem;
		font-weight: 700;
		letter-spacing: -.03em;
		text-align: center;
		vertical-align: top;

		&.prev {
			color: $headings-text;
		}

		a {
			color: #222524;
		}

		&.active {
			a {
				color: $primary-color;

				&:hover{
					color: $primary-color;
				}
			}

			+ li a {
				color: #919292;

				&:hover{
					color: $primary-color;
				}
			}
		}

		&.disabled a {
			color: #919292;
		}

		&:not(:first-child) {
			margin-left: 5.8rem;

			&:before {
				position: absolute;
				left: -3.3rem;
				top: -.8rem;
				content: '\e81a';
				color: #999;
				font-family: "porto";
				font-size: 3rem;
			}
		}

		&:last-child {
			pointer-events: none;
		}
	}
}

.checkout-steps {
	li {
		margin-bottom: 3rem;

		form {
			margin-top: 1.5rem;
			margin-bottom: 0;
			padding-bottom: 2rem;
			border-bottom: 1px solid #ccc;

			&:last-of-type {
				padding-bottom: .1rem;
				border-bottom: 0;
			}

			p {
				margin-bottom: 1rem;
				color: #777;
				font-size: 1.3rem;
			}
		}

		.form-footer {
			margin-top: 1rem;
			margin-bottom: 1rem;
		}
	}
}

.step-title {
	@include clearfix;

	margin-bottom: 0;
	color: #222529;
	font-size: 2.2rem;

	.step-title-edit {
		margin-top: .4rem;
		float: right;
		font-size: 1.4rem;
	}
}

.form-login-toggle,
.form-coupon-toggle {
	font-size: 13px;
    font-weight: 500;
	letter-spacing: -.025em;
	line-height: 24px;
	
	a {
		color: #222524;
	}
}

.form-login-toggle {
	margin-bottom: .8rem;
}

.form-coupon-toggle {
	margin-bottom: 2.3rem;
}

.table-step-shipping {
	max-width: 600px;

	tr {
		td {
			padding: 1.8rem 1.5rem;
			border: 0;
			border-bottom: 1px solid #ccc;
		}
	}
}

.checkout-steps-action {
	@include clearfix;

	.btn {
		min-width: 70px;
		text-align: center;
	}
}

.order-summary {
	margin-bottom: 3rem;
	padding: 3rem;
	border: 2px solid #e7e7e7;

	.custom-radio .custom-control-input {
		width: 102px;
	}

	h3 {
		margin-bottom: 1.9rem;
		font-size: 1.6rem;
		letter-spacing: -.01em;
	}

	h4 {
		margin-bottom: 0;
		font-size: 1.4rem;
		font-weight: 600;
		letter-spacing: -.01em;
		line-height: 19px;
	}

	.info-box {
		p {
			padding: 11px 3px;
			list-style-position: inside;
			text-align: left;
			margin-bottom: 9px;
			font-size: 16px;
			font-weight: 500;
			line-height: 24px;
			color: #2f6473;
		}
