.checkout-info-box {
	margin-bottom: 3rem;
	font-size: 1.3rem;
	line-height: 2.7rem;

	.step-title {
		margin-bottom: 1.8rem;
	}
}

.checkout-container {
	padding-bottom: 7px;

	.shipping-info {
		padding-bottom: 3px;
	}

	.select-custom {
		.form-control {
			font-size: 1.4rem;
		}
	}
	
	label {
		margin-bottom: .6rem;
		color: $body-text;
		font-family: $font-family;
		font-size: 1.4rem;
		font-weight: 400;
		letter-spacing: -.01em;
	}

	.checkout-discount {
		.form-control {
			margin-right: 1.4rem;
			height: 33px;
			font-size: 1.2rem;
		}

		.btn.btn-sm {
			height: 33px;
		}
	}

	.step-title {
		letter-spacing: -.01em;
	}

	select.form-control:not([size]):not([multiple]),
	.form-control {
		height: 49px;
	}

	.select-custom,
	.form-group {
		margin-bottom: 1.7rem;
	}

	.checkout-steps {
		.custom-control-label {
			&::before,
			&::after {
				top: 0px;
			}
		}

		.select-custom::after {
			top: 66%;
		}
	}

	.custom-control {
		margin-top: 3.3rem;
		margin-bottom: 0;
	}

	.order-comments {
		padding-top: 6px;
	}

	textarea.form-control {
		min-height: 125px;
	}

