// Demo35 Shop page

body[lang="ar"] .main-content,
body[lang="ar"] .sidebar-shop {
    text-align: right;
}

.main-content,
.sidebar-shop {

    padding-top: 3.5rem;
    padding-bottom: 4rem;
}

.main-content {
    padding-bottom: 3rem;
}

.category-banner {
    .rotate-text {
        right: 0.68552em;
    }
}

.shop-off-canvas {
    .sidebar-shop {
        .sidebar-wrapper {
            border: 0;
        }

        .widget {
            padding: 2rem 0;

            &:not(:last-child) {
                border-bottom: 1px solid #f4f4f4;
                border-radius: 0;
            }
        }
    }
}

.sidebar-shop {
    .widget:not(:last-child) {
        border-bottom: 1px solid #e7e7e7;
    }

    .widget-featured {
        padding-bottom: 2rem;
    }

    .product-widget {
        .product-title {
            color: #333;
            font-family: $second-font-family;
            letter-spacing: 0;
        }

        .price-box {
            font-family: $second-font-family;
        }

        .product-details {
            padding-right: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.cat-list li {
    margin-bottom: 1rem;
    line-height: 1.45;

    .product-count {
        font-size: 1.3rem;
        font-weight: 400;
    }
}

.widget-brand .cat-list li {
    font-size: 1.4rem;
    line-height: 20px;

    // a {
    //     font-weight: 400;
    // }
}

.filter-price-action .btn {
    padding: .4em 1.5rem;
    line-height: 1.5;
}

.toolbox label {
    font-family: $second-font-family;
}

.toolbox-pagination {
    margin-top: 1.4rem;
}

.page-link-btn {
    padding: 0;
    border: 1px solid #ccc;
    width: 31px;
    height: 31px;
}

// Product page

.product-single-container {
    padding-top: 3.5rem;
}

.product-single-details {
    .product-title {
        margin-bottom: .9rem;
    }

    .old-price {
        margin-right: 2px;
    }

    .product-desc {
        margin-bottom: 1.6rem;
        font-family: $second-font-family;
        line-height: 1.6872;
    }

    .single-info-list {
        font-family: $second-font-family;
    }

    .ratings-container .product-ratings {
        flex: 0;
    }
}

.product-category:hover {
    color: #1d2127;
    //text-decoration: underline;
}

.add-wishlist {
    font-family: $font-family;

    i {
        margin-top: -2px;
        font-size: 1.8rem;
        line-height: 1;
    }
}

// Producrt Single Gallery
.product-single-gallery {
    margin-bottom: 3.2rem;
}

.pg-vertical .owl-dot::before,
.product-both-info .inner,
.product-single-info .inner,
.product-single-grid .inner,
.product-single-carousel,
.prod-thumbnail img {
    border: 1px solid #f4f4f4;
}


// Product Description
.product-single-tabs .nav.nav-tabs .nav-item .nav-link {
    padding: 7px 0;
    font-family: $second-font-family;
    line-height: 22px;
}

.product-desc-content p {
    margin-bottom: 2.2rem;
}

// Products Section
.products-section {
    h2 {
        font-size: 1.6rem;
    }

    .product-title {
        margin-bottom: 7px;
    }

    .owl-carousel.dots-top .owl-dots span {
        border-color: rgba(46, 104, 60, .4);
    }

    .products-slider .product-default {
        padding: 0;
    }

    .owl-carousel.dots-top .owl-dots {
        margin-bottom: 2.5rem;
    }
}

// Responsive
@include mq(lg) {
    .sidebar-shop {
        .sidebar-wrapper {
            border: 1px solid #e7e7e7;
        }

        .widget:not(:last-child) {
            border-bottom-left-radius: 7px;
            border-bottom-right-radius: 7px;
        }
    }
}

@include mq(md) {
    .products-section {
        padding-bottom: 3.3rem;
    }
}

@media (max-width: 1440px) and (min-width: 992px) {
    .sidebar-shop .product-widget figure {
        max-width: 60px;
    }
}

@media (max-width: 1200px) {
    .sidebar-shop .widget-title {
        font-size: 1.4rem;
    }

    .widget .owl-carousel .owl-nav {
        top: -3.4rem;
    }
}

@media (max-width: 991px) {
    .shop-off-canvas .sidebar-wrapper {
        padding: 0rem;
    }
}